.formContainer {
  width: 100%;
}
.loginMessage {
  display: block;
  color: #fd4444;
  width: 300px;
  line-height: 28px;
  font-weight: 600;
  font-size: 14px;
  line-height: 28px;
  margin-bottom: 28px;
}

.loginMessageVissible {
  display: block;
  margin-bottom: 14px;
}

.label {
  width: 360px;
  font-size: 14px;
  line-height: 16px;
  margin-bottom: 8px;
}

.input {
  width: 100% !important;
  height: 48px !important;
  background: #ffffff !important;
  border: 1px solid #ced4da !important;
  border-radius: 0px !important;
  margin-bottom: 16px !important;
}

.button {
  width: 100%;
  height: 48px;
  margin-top: 24px;
  color: white;
  background-color: #343a40;
  border-radius: 0px;
  border-style: none;
}

.button:hover {
  background-color: #495057;
}
